 width: 140px;\n  height: 25px;\n  background-color: #27292d;\n}\n.turn_frame{\n  display: inline-block;\n  width: 50px;\n  height: 30px;\n  border-radius: 5px;\n  line-height: 30px;\n  cursor: pointer;\n  font-size: 12px;\n}\n.turn_confirm{\n  background-color: #f9a32a;\n  color: #fff;\n}\n.turn_quit{\n  background-color: #dcdcdc;\n  color: #000;\n}\n.trunname_ed{\n  background:#53585d;\n}\n.trunname_ing{\n  display: none;\n  background-color: #81858a;\n}\n.turn_set_detail{\n  background-color: #16181d;\n  height: 60px;\n  font-size: 12px;\n  display: none;\n}\n.turn_set_detail li{\n  height: 20px;\n  margin: 5px 0;\n  line-height: 20px;\n  cursor: pointer;\n}\n.turn_set_detail li:hover{\n  background-color: #7f5b2f;\n}\n.tech_range{\n  width: 200px;\n  height: 25px;\n  background-color: #838383;\n  border-radius: 3px;\n}\n.tech_range_detail{\n  width:40px;\n  height: 25px;\n  border-radius: 3px;\n  background-color: #f9a32a;\n  float: left;\n}\n.tech_range_num{\n  float: right;\n  font-size: 12px;\n  line-height: 25px;\n}\n#set_begin{\n  opacity: 0;\n  height:20px;\n  width:288px\n}\n.set_time_begin{\n  position: absolute;\n  top:2px;\n  left:100px;\n  font-size: 12px;\n}\n.edit_ring{\n  width: 120px;\n  height: 120px;\n  background-color: #181b1f;\n  border-radius: 60px;\n  text-align: center;\n  line-height: 120px;\n  font-size: 20px;\n}\n.edit_detail_top{\n  margin: 10px 100px;\n}\n.edit_detail_top li{\n  float: left;\n  text-align: center;\n  position: relative;\n}\n.edit_detail_mid{\n  width: 50px;\n  font-size: 20px;\n}\n.edit_detail_bt{\n  margin: 10px 100px 10px;\n  height: 130px;\n  overflow-y: scroll;\n}\n.edit_btn_mid{\n  display: inline-block;\n  width: 20px;\n  font-size: 18px;\n  text-align: center;\n}\n.add_edit_list{\n  color: #fe3507;\n  font-size: 12px;\n  margin: 0 100px;\n  cursor: pointer;\n}\n.made_winer{\n  height: 30px;\n  background: url(" + require("../../static/images/lose.png") + ") no-repeat center;\n  cursor: pointer;\n}\n.winer_active{\n  background: url(" + require("../../static/images/winer.png") + ") no-repeat center !important;\n}\n.add_winer{\n  background-color: #f9a32a;\n}\n.edit_detail_table{\n  width: 300px;\n  max-height: 200px;\n  margin: 10px auto;\n  text-align: center;\n  background-color: #1c1d21;\n  overflow-y:scroll;\n}\n.edit_detail_table td{\n  border: 1px solid #404244;\n  height: 40px;\n}\n.compare_active{\n  color: #f9a32a;\n}\n.add_guan{\n  position: absolute;\n  top: 80px;\n  right:10px;\n}\n.tech_intro{\n  background: rgba(52,57,63,0.9);\n  padding: 0 20px 30px;\n  color: #fff;\n  font-size: 12px;\n}\n.tech_intro h2{\n  font-size: 18px;\n}\n.float_checkbox{\n  display: inline-block;\n  width: 15px;\n  height: 15px;\n  border: 1px solid #53585d;\n  border-radius: 2px;\n  position: absolute;\n  top:0;\n  left:0;\n}\n.check_gou{\n  display: inline-block;\n  width: 15px;\n  height: 15px;\n  text-align: center;\n  color: #f9a32a;\n}\n#show_app{\n  opacity: 0;\n  z-index: 99999;\n  width:15px;\n  height: 15px;\n}\n", "", {"version":3,"sources":["/./src/views/techPic.vue.style"],"names":[],"mappings":";AAm8BA;EACA,eAAA;CACA;AACA;EACA,mBAAA;CACA;AACA;EACA,aAAA;EACA,eAAA;EACA,0BAAA;EACA,YAAA;EACA,kBAAA;CACA;AACA;EACA,mBAAA;EACA,MAAA;EACA,OAAA;CACA;AACA;EACA,YAAA;EACA,mBAAA;CACA;AACA;EACA,cAAA;EACA,iBAAA;CACA;AACA;EACA,YAAA;CACA;AACA;EACA,cAAA;EACA,+BAAA;EACA,qBAAA;EACA,kBAAA;CACA;AACA;EACA,aAAA;EACA,cAAA;EACA,YAAA;CACA;AACA;EACA,aAAA;EACA,aAAA;EACA,YAAA;CACA;AACA;EACA,qBAAA;EACA,kCAAA;CACA;AACA;EACA,YAAA;CACA;AACA;EACA,aAAA;CACA;AACA;EACA,aAAA;EACA,aAAA;EACA,mBAAA;EACA,gBAAA;EACA,iBAAA;CACA;AACA;EACA,mBAAA;EACA,aAAA;EACA,aAAA;EACA,QAAA;EACA,OAAA;EACA,mBAAA;EACA,kBAAA;EACA,gBAAA;CACA;AACA;EACA,gBAAA;EACA,oBAAA;CACA;AACA;EACA,gBAAA;EACA,mBAAA;CACA;AACA;EACA,YAAA;EACA,mBAAA;CACA;AACA;EACA,YAAA;EACA,gBAAA;CACA;AACA;EACA,oBAAA;CACA;AACA;EACA,YAAA;EACA,mBAAA;CACA;AACA;EACA,mBAAA;CACA;AACA;EACA,sBAAA;EACA,WAAA;EACA,aAAA;EACA,0BAAA;CACA;AACA;EACA,cAAA;EACA,kBAAA;EACA,+BAAA;EACA,kBAAA;EA