									<img src="../../static/images/mg_del.png" title="移出" class="u-tuji-del" @click="deleteimg" indexs="{{$index}}">
									<div class="tips">
										<div class="attention"></div>
									</div>
								</div>
								<p class="l col350 csp" id="addPicture" @click="addPicture">+添加一张照片</p>
							</div>
