<script type="text/javascript">
   import topNav from '../components/topNav.vue'
  export default {
    data () {
	    var new_info={
	    	title:'',
	    	type:'',
	    	timerDate:'',
	    	creater:'',
	    	icon:'',
	    	source:'',
	    	remark:'',
	    	imgs:[],
	    	keyword:'',
	    	cover:'',
	    	videoUrl:'',
	    	eventId:'',
	    	isPublished:''
	    }
        return {
    	 	new_infos:[new_info]
        }
  	},
  	components: {
      topNav
         
    },
  	ready:function(){
  		var _this= this;
  		var obj = this.new_infos[0];
  		obj.eventId=window.sessionStorage.getItem("eventid");
  	    console.log(obj.eventId);
  		//日历控件
  		$('#begintime').datetimepicker({
	        	format:"Y-m-d H:i:s",   
			    yearStart:2000,     
			    yearEnd:2050, 
			    minDate:new Date(),
			    step:10
	        });
       	$.datetimepicker.setLocale('ch');
       	//绑定编辑器
       	$('#brief').editable({
				inlineMode: false,
				theme: 'dark', 
				alwaysBlank: true,
				language: "zh_cn",
				placeholder: '请输入详细内容'
			});
    	//类型选择
    	$('.m-n-select').change(function(event) {
    		var lx = $(".m-n-select option:selected").val();
    		if(lx==3){
    			$('.m-n-lx').eq(2).removeClass('dn').siblings('.m-n-lx').addClass('dn');
    		}
    		else if(lx==4){
				$('.m-n-lx').eq(1).removeClass('dn').siblings('.m-n-lx').addClass('dn');
    		}
    		else if(lx==1){
				$('.m-n-lx').eq(0).removeClass('dn').siblings('.m-n-lx').addClass('dn');
    		}
    	});
    	$('#pic').diyUpload({
    		url:'http://wy.oetapi.wangyuhudong.com/file/upload',
			success:function( data ) {
				console.info( data );	
				_this.new_infos[0].icon=data.object.src;
			},
			error:function( err ) {
				console.info( err );	
			},
			buttonText : '选择图片',	
			chunked:true,
			// 分片大小
			chunkSize:512 * 1024,
			//最大上传的文件数量, 总文件大小,单个文件大小(单位字节);
			fileNumLimit:1,
			fileSizeLimit:500000 * 1024,
			fileSingleSizeLimit:50000 * 1024,
			accept: {}
    	});
    	$('#firstpic').diyUpload({
    		url:'http://wy.oetapi.wangyuhudong.com/file/upload',
			success:function( data ) {
				console.info( data );	
				_this.new_infos[0].cover=data.object.src;
			},
			error:function( err ) {
				console.info( err );	
			},
			buttonText : '选择图片',	
			chunked:true,
			// 分片大小
			chunkSize:512 * 1024,
			//最大上传的文件数量, 总文件大小,单个文件大小(单位字节);
			fileNumLimit:1,
			fileSizeLimit:500000 * 1024,
			fileSingleSizeLimit:50000 * 1024,
			accept: {}
    	});
    	var imgsobj = new Object();
    	imgsobj.img='';
    	imgsobj.remark='';
    	obj.imgs.push(imgsobj);
    	console.log(obj.imgs);
    	_this.$nextTick(function(){
	    		$('#tuji0').diyUpload({
	    		url:'http://wy.oetapi.wangyuhudong.com/file/upload',
				success:function( data ) {
					console.info( data );			
					$('#tuji0').siblings('input[type="hidden"]').val(data.object.src);
					obj.imgs[0].img = data.object.src;
					$('#tuji0').parents('.tuji').find('.attention').hide();
				},
				error:function( err ) {
					console.info( err );	
				},
				buttonText : '选择图片',	
				chunked:true,
				// 分片大小
				chunkSize:512 * 1024,
				//最大上传的文件数量, 总文件大小,单个文件大小(单位字节);
				fileNumLimit:1,
				fileSizeLimit:500000 * 1024,
				fileSingleSizeLimit:50000 * 1024,
				accept: {}
	    	});
    	})
  	},
  	methods:{
  		deleteimg:function(){
  			var aaa = this.new_infos[0];
  			var _this = $(event.target);
  			var indexs = _this.attr("indexs");
  			this.new_infos[0].imgs.splice(indexs,1);
	  			this.$nextTick(function(){
	  				$('#tuji0').diyUpload({
		    		url:'http://wy.oetapi.wangyuhudong.com/file/upload',
					success:function( data ) {
						console.info( data );			
						$('#tuji0').siblings('input[type="hidden"]').val(data.object.src);
						aaa.imgs[0].img = data.object.src;
						$('#tuji0').parents('.tuji').find('.attention').hide();
					},
					error:function( err ) {
						console.info( err );	
					},
					buttonText : '选择图片',	
					chunked:true,
					// 分片大小
					chunkSize:512 * 1024,
					//最大上传的文件数量, 总文件大小,单个文件大小(单位字节);
					fileNumLimit:1,
					fileSizeLimit:500000 * 1024,
					fileSingleSizeLimit:50000 * 1024,
					accept: {}
		    	});
  			})
  		},
  		addPicture:function(){
  			var aaa = this.new_infos[0];
  			var imgsobj = new Object();
	    	imgsobj.img='';
	    	imgsobj.remark='';
	    	this.new_infos[0].imgs.push(imgsobj);
	    	console.log("")
  			this.$nextTick(function(){
  			    var l = this.new_infos[0].imgs.length;
  			    var nn = l-1;
	  			$('#tuji'+nn).diyUpload({
	    			url:'http://wy.oetapi.wangyuhudong.com/file/upload',
					success:function( data ) {
						console.info( data );
						aaa.imgs[nn].img =  data.object.src;	
						$('#tuji'+nn).siblings('input[type="hidden"]').val(data.object.src);
						$('#tuji'+nn).parents('.tuji').find('.attention').hide();
					},
					error:function( err ) {
						console.info( err );	
					},
					buttonText : '选择图片',	
					chunked:true,
					// 分片大小
					chunkSize:512 * 1024,
					//最大上传的文件数量, 总文件大小,单个文件大小(单位字节);
					fileNumLimit:1,
					fileSizeLimit:500000 * 1024,
					fileSingleSizeLimit:50000 * 1024,
					accept: {}
	    		})
    		    $('.tuji').each(function(index, el) {
					$(this).hover(function() {
					   	var tuJiLen = $('.tuji').length;
					   	// var a = index;
					   	if(tuJiLen!=1){
							$(this).find('.u-tuji-del').show();
						}
					}, function() {
						$(this).find('.u-tuji-del').hide();
					});	
				});
  			})
  		},
  		openvideolay:function(event){
  			var _this = this;
  			layer.open({
			  type: 1,
			  skin: 'layui-layer-rim', //加上边框
			  area: ['600px', '250px'], //宽高
			  content:'<form id="form-upload" action="">'+ 
	  				  '<div style="width:80%; margin:40px auto 0;" >'+
		  			  '<label style="color:#000000;" for="addvideo">视频：</label>'+
		  			  '<input type="file" class="spinput" name="file" accept="video/*" id="addvideo"/>'+
		  			  '</div>'+
		  			  '<div style="text-align:center;margin:20px 0 0 0;"><button type="button" style="width:10%; height:34px; line-height:34px;" id="check">校验</button><button style="display:none;width:10%;height:34px;line-height:34px;" id="upload">上传</button></div>'+
		  			  '</form>'
			});	
  			var $fileForm = $('#form-upload');
			// 初始化操作按钮
			var checkFile = false;
			function initUploadBtn() {
				var filename = $fileForm.find('[name="file"]').val();
				var $check = $fileForm.find('#check');
				var $upload = $fileForm.find('#upload');
				if(checkFile && checkFile == filename) {// 文件已校验,可进行上传
					$check.hide();
					$upload.show();
				} else {// 未校验或文件变更
					$check.show();
					$upload.hide();
				}
			}
			
			$fileForm.find('[name="file"]').change();	
			$('#check').off('click');
			$('#check').on('click', function(ev) {
				var $file = $fileForm.find('[name="file"]');
				var filename = $file.val();
				var fileChecked = true;
				if(filename.length <= 0) {
					console.log("请先选择文件");
					fileChecked = false;
				} else {
					checkFile = filename;
				}
				if(fileChecked){
					var $this = $(this);
					$this.prop('disabled', true);
					console.log("文件校验中, 请稍等 ...");
					_this.$http.get('http://wy.oetapi.wangyuhudong.com/file/upYunFormSignature?filename='+filename).then(function(response){
						var res = response.data.object;
						//初始化校验数据
						var action = res.action;
						var signature = res.signature;
						var policy = res.policy;
						var params = res.params;
						
						function hidden(name, value) {
							return '<input type="hidden" name="' + name + '" value="' + value + '" />';
						}
						$fileForm.attr('action', action);
						if(params){
							for(var k in params){
								var value = params[k];
								$fileForm.append(hidden(k,value));
							}
						}

						$fileForm.append(hidden('signature', signature));
						$fileForm.append(hidden('policy', policy));

						// 初始化表单操作按钮
						checkFile = res.filename;
						initUploadBtn();

					}, function(response){
						console.log(response);
					})	
				}
			});
			$('#upload').off('click');
			$('#upload').on('click', function(){
				var $this = $(this);
				$this.prop('disabled', true);
				console.log("上传中，请稍等 ..");
				$fileForm.ajaxSubmit({
					'url': $fileForm.attr('action'),
					'type': 'post',
					'dataType': 'json',
					'success': function(d) {
						var code = d.code;
						if(code == 200) {
							$('#addinfo').find('input[name="videoUrlUpload"]').val(d.url).change();
						} else {
							console.log('操作失败,原因:' + d.message);
						}
						console.log(d);
					},
					complete: function() {
						console.log('完成');
						layer.closeAll();
					}
				});
			})
  		},
  		savedraft:function(event){//存草稿
  			this.new_infos[0].isPublished = 0;
  			var typenum = this.new_infos[0].type;
  			var newsobj = this.new_infos[0];
  			var isicon = this.new_infos[0].icon;
  			var iscover = this.new_infos[0].cover;
  			function errorPlacement(mes,element){
		    	var errorTips=element.parents(".m-lst").find('div.attention');
		    	if(mes!=""){
		    		errorTips.show().html(mes);
		    	}else{
		    		errorTips.hide();
		    	}			    				    	
		    }
		    function tujierrorPlacement(mes,element){
		    	var errorTips=element.parents(".tuji").find('div.attention');
		    	if(mes!=""){
		    		errorTips.show().html(mes);
		    	}else{
		    		errorTips.hide();
		    	}			    				    	
		    }		
  			function formValidate(){
  				var valid=true;
  				$('.m-addzx [required]').each(function(){
  					var $this=$(this);
  					var value=$this.val(),name=$this.attr('title');	
  					var text='';
  					if(value==null||value==''){
						text=name+'不能为空！';
						valid=false;
					}
					errorPlacement(text,$this);
  				})
  				var $slt = $('#pic');
  				if(isicon == ''){
  					valid = false;
  					errorPlacement('请先上传缩略图！',$slt);
  				}
  				else{
  					errorPlacement('',$slt);
  				}
  				if(typenum==1){
  					var $this = $('.m-addzx [twsource]');
  				    var twval = $this.val();
  				    if(twval==null||twval==''){
  				    	valid = false;
  				    	errorPlacement('资讯来源不能为空！',$this);   	
  				    }
  				    else{
  				    	errorPlacement('',$this);
  				    }
  				    var $thistwrek = $('.m-addzx [twremark]').find('.froala-element'),
  				        thistwrek = $thistwrek.html();
			        if(thistwrek!="<p><br></p>"){
			        	errorPlacement('',$thistwrek);
			        	newsobj.remark=thistwrek;   	
			        }
			        else{
			        	valid = false;
  				    	errorPlacement('详细内容不能为空！',$thistwrek);
			        }

  				}
  				if(typenum==4){
  					var $thiskey = $('.m-addzx [spkeyword]'),
  						thiskey = $thiskey.val();
  					if(thiskey==null||thiskey==''){
  						valid = false;
  				    	errorPlacement('关键字不能为空！',$thiskey);
  					}
  					else{
  						errorPlacement('',$thiskey);
  					}
  					var $thisspurl = $('.m-addzx [spurl]'),
  						thisspurl = $thisspurl.val();
  					if(thisspurl==null||thisspurl==''){
  						valid = false;
  				    	errorPlacement('视频地址不能为空！',$thisspurl);
  					}
  					else{
  						errorPlacement('',$thisspurl);
  					}
  					var $st = $('#firstpic');
  					if(iscover == ''){
  						valid = false;
						errorPlacement('首图不能为空！',$st);
  					}
  					else{
  						errorPlacement('',$st);
  					}
  				}
  				if(typenum==3){
  					var $tjsource = $('.m-addzx [tjsource]'),
  						tjsource = $tjsource.val();
  					if(tjsource==null||tjsource==''){
  						valid = false;
  				    	errorPlacement('资讯来源不能为空！',$tjsource);
  					}
  					else{
  						errorPlacement('',$tjsource);
  					}
  					
	  				$('.tuji').each(function(index){
	  					var hidipt = $(this).find('input[type="hidden"]').val();
	  					var hid = $(this).find('input[type="hidden"]');
	  					if(hidipt==''){
	  						tujierrorPlacement('请先上传图片',hid);
	  						valid = false;
	  					}
	  				 	else{
	  				 		tujierrorPlacement('',hid);
	  				 		var n = $(this).find('.u-n-ttr');
	  						var rem = $(this).find('.u-n-ttr').val();
	  						if(rem==''){
	  							tujierrorPlacement('图片说明不能为空！',n);
	  							valid = false;
		  					}
		  				 	else{
		  				 		tujierrorPlacement('',n);
		  				 		// newsobj.imgs[index].remark = rem;
		  				 	}
	  				 	}
	  				})
  				}
  				return valid;	
  			}
  				
  			if(formValidate()){
  				$('.tuji').each(function(index){
  					var rem = $(this).find('.u-n-ttr').val();
  					newsobj.imgs[index].remark = rem;
  				});

  				var jsonInfos = JSON.stringify(newsobj);
  				var parm = new Object();
  				parm.jsonInfo = jsonInfos;

  				this.$http.post('event/information/save',parm).then(function(successResponse){
	  				var code = successResponse.data.code;
	  				console.log(code);
	  				this.$route.router.go({path: '/newslist'});
	  			}, function(errorResponse){
	  				console.log("通讯错误")
	  			})
  			}
  		},
  		savetrue:function(event){//确定发布
  			this.new_infos[0].isPublished = 1;
  			var typenum = this.new_infos[0].type;
  			var newsobj = this.new_infos[0];
  			var isicon = this.new_infos[0].icon;
  			var iscover = this.new_infos[0].cover;
  			function errorPlacement(mes,element){
		    	var errorTips=element.parents(".m-lst").find('div.attention');
		    	if(mes!=""){
		    		errorTips.show().html(mes);
		    	}else{
		    		errorTips.hide();
		    	}			    				    	
		    }
		    function tujierrorPlacement(mes,element){
		    	var errorTips=element.parents(".tuji").find('div.attention');
		    	if(mes!=""){
		    		errorTips.show().html(mes);
		    	}else{
		    		errorTips.hide();
		    	}			    				    	
		    }		
  			function formValidate(){
  				var valid=true;
  				$('.m-addzx [required]').each(function(){
  					var $this=$(this);
  					var value=$this.val(),name=$this.attr('title');	
  					var text='';
  					if(value==null||value==''){
						text=name+'不能为空！';
						valid=false;
					}
					errorPlacement(text,$this);
  				})
  				var $slt = $('#pic');
  				if(isicon == ''){
  					valid = false;
  					errorPlacement('请先上传缩略图！',$slt);
  				}
  				else{
  					errorPlacement('',$slt);
  				}
  				if(typenum==1){
  					var $this = $('.m-addzx [twsource]');
  				    var twval = $this.val();
  				    if(twval==null||twval==''){
  				    	valid = false;
  				    	errorPlacement('资讯来源不能为空！',$this);   	
  				    }
  				    else{
  				    	errorPlacement('',$this);
  				    }
  				    var $thistwrek = $('.m-addzx [twremark]').find('.froala-element'),
  				        thistwrek = $thistwrek.html();
			        if(thistwrek!="<p><br></p>"){
			        	errorPlacement('',$thistwrek);
			        	newsobj.remark=thistwrek;   	
			        }
			        else{
			        	valid = false;
  				    	errorPlacement('详细内容不能为空！',$thistwrek);
			        }

  				}
  				if(typenum==4){
  					var $thiskey = $('.m-addzx [spkeyword]'),
  						thiskey = $thiskey.val();
  					if(thiskey==null||thiskey==''){
  						valid = false;
  				    	errorPlacement('关键字不能为空！',$thiskey);
  					}
  					else{
  						errorPlacement('',$thiskey);
  					}
  					var $thisspurl = $('.m-addzx [spurl]'),
  						thisspurl = $thisspurl.val();
  					if(thisspurl==null||thisspurl==''){
  						valid = false;
  				    	errorPlacement('视频地址不能为空！',$thisspurl);
  					}
  					else{
  						errorPlacement('',$thisspurl);
  					}
  					var $st = $('#firstpic');
  					if(iscover == ''){
  						valid = false;
						errorPlacement('首图不能为空！',$st);
  					}
  					else{
  						errorPlacement('',$st);
  					}
  				}
  				if(typenum==3){
  					var $tjsource = $('.m-addzx [tjsource]'),
  						tjsource = $tjsource.val();
  					if(tjsource==null||tjsource==''){
  						valid = false;
  				    	errorPlacement('资讯来源不能为空！',$tjsource);
  					}
  					else{
  						errorPlacement('',$tjsource);
  					}
  					
	  				$('.tuji').each(function(index){
	  					var hidipt = $(this).find('input[type="hidden"]').val();
	  					var hid = $(this).find('input[type="hidden"]');
	  					if(hidipt==''){
	  						tujierrorPlacement('请先上传图片',hid);
	  						valid = false;
	  					}
	  				 	else{
	  				 		tujierrorPlacement('',hid);
	  				 		var n = $(this).find('.u-n-ttr');
	  						var rem = $(this).find('.u-n-ttr').val();
	  						if(rem==''){
	  							tujierrorPlacement('图片说明不能为空！',n);
	  							valid = false;
		  					}
		  				 	else{
		  				 		tujierrorPlacement('',n);
		  				 		// newsobj.imgs[index].remark = rem;
		  				 	}
	  				 	}
	  				})
  				}
  				return valid;	
  			}
  				
  			if(formValidate()){
  				$('.tuji').each(function(index){
  					var rem = $(this).find('.u-n-ttr').val();
  					newsobj.imgs[index].remark = rem;
  				});

  				var jsonInfos = JSON.stringify(newsobj);
  				var parm = new Object();
  				parm.jsonInfo = jsonInfos;

  				this.$http.post('event/information/save',parm).then(function(successResponse){
	  				var code = successResponse.data.code;
	  				console.log(code);
	  				this.$nextTick(function(){
	  					this.$route.router.go({path: '/newslist'});
	  				})
	  			}, function(errorResponse){
	  				console.log("通讯错误")
	  			})
  			}
  		},
  		formreset:function(){
  			$('#addinfo input').val('');
  			$('#addinfo textarea').val('');
  		}
  	}
}
</script>


